h1, h2, h3, h4, h5, h6 {
  line-height: 1;
}

.navbar {
  font-style: "Roboto";
  margin: 0;
  padding: 0;
  height: 42px;
  background-color: #1d4b56;
  color: white;
  width: 100%;
  position: fixed;
  overflow: auto;
}

.nav-content {
  margin: 0 auto;
  width: 80%;
  max-width: 600px;
}

.nav-content .nav-item {
  color: white;
  text-decoration: none;
  cursor: pointer;
  display: block;
  float: left;
  padding-right: 40px;
  height: 100%;

  font-size: 18px;
  font-weight: 500px;

  padding-top: 9px;
  padding-right: 10px;
  padding-bottom: 9px;
  padding-left: 10px;
}

.nav-content .nav-item-active {
  background-color: #EEE;
  color: black;

}

.nav-content .nav-item:hover {
  background-color: #4b7783;
}

.nav-content .nav-item-active:hover {
  background-color: #EEE;
  color: black;
}

#title {
  color: white;
  font-size: 28px;

  text-shadow: 1px 1px 1px black;

  text-decoration: none;

  display: -webkit-flex;

  display: flex;
  float: left;
  -webkit-align-items: center;
          align-items: center;

  padding-right: 35px;
  height: 100%;
  margin-right: 5px;
}

