.game {
  margin: 0 auto;
  height: 350px;
  width: 250px;
  display: grid;
  grid-template-rows: 50px 1fr 50px;
  background-color:#255C69;
  font-family: "Roboto";
  box-shadow: 0 2px 3px rgba(0,0,0,0.12), 0 1px 2px rgba(0,0,0,0.24);
}

.board {
  margin: 0 auto;
  width: 250px;
  height: 250px;
  background-color: #bcbcbc;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 5px;
}

.space {
  color: black;
  background-color: white;
  font-size: 57px;
  cursor: pointer;
  text-align: center;
}

.space:focus {
  outline: none;
}

.status {
  color: white;
  background-color: #255C69;
  display: block;
  -webkit-align-self: center;
          align-self: center;
  text-align: center;
}

#statusShape {
  margin-left: 5px;
  float: left
}

#statusTurn {
  margin-right: 5px;
  float: right
}

#statusTitle {
  text-align: center;
}

.panel {
  color: white;
  background-color: #255C69;
  display: flow-root;
  -webkit-align-self: center;
          align-self: center;
  text-align: center
}

